.wrapper {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login {
    display: flex;
    flex-direction: column;
    background: #724188;
    height: 480px;
    width: 25%;
    padding: 30px;
    border-radius: 10px;
}

.username {
    display: flex;
    flex-direction: column;
    margin: 50px 0;
    margin-top: 30px;
}

.label {
    height: 30px;
    display: flex;
    align-items: center;
}

.label h4,
.icon {
    color: #fff;
    font-size: 20px;
}

.label h4 {
    margin-top: 6px;
}

.icon {
    margin-top: 3px;
    margin-right: 6px;
    font-size: 16px;
}

.password {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.input {
    background-color: transparent;
    color: #fff;
    outline: none;
    border: none;
    border-bottom: 1px solid #fff;
    font-size: 20px;
    margin-top: 15px;
}

.submit-btn {
    display: block;
    margin: 10px 0;
    padding: 10px 30px;
    border-radius: 20px;
    border: 2px solid transparent;
    font-size: 16px;
    font-weight: 600;
    color: #724188;
    cursor: pointer;
    user-select: none;

    &:hover {
        border: 2px solid #fff;
        background-color: #724188;
        color: #fff;
    }

    &.disabled {
        pointer-events: none;
        --webkit-user-select: none;
        background-color: #717171;
        color: #fff;
    }
}