@import url('https://fonts.googleapis.com/css?family=Coiny');

.wrapper {
    display: flex;
    align-items: center;
    width: 300px;
    height: 70px;
    position: fixed;
    top: 20px;
    right: 0;
    padding: 12px;
    padding-left: 6px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: -1px 4px 14px 1px #ccc;
    animation: hide 1s .2s linear;

    &.success-wrapper .vertical{
        background-color: #41a145;
    }

} 

@keyframes hide {
    to {
       display: none;
    }
   
}

