.vertical{
    margin-right: 12px;
    height: 90%;
    width: 3px;
    border-radius: 10px;
    background-color: #f44236;
}

.icon {
    font-size: 24px;
    margin-right: 12px;
    color: #f44236;
    
    &.success-icon {
        color: #41a145;
    }
}

.detail {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.status,
.description {
    font-family: 'Coiny', cursive;
}

.status {
    font-size: 16px;
    color: #f44236;
}

.status.success {
    color: #41a145;
}

.description {
    color: #333;
    font-size: 14px;
}

.close-icon {
    margin-left: 24px;
    margin-top: -20px;
    font-size: 20px;
    cursor: pointer;

    &:hover {
        opacity: .7;
    }
}